<template>
  <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
    <el-tab-pane v-for="item in items" :key="item.message" :name="item.message">
      <span slot="label">{{ item.message }}</span>
        <el-row class="el-row-wrap" type="flex">
          <el-row class="el-row-flower" type="flex" align="middle" v-for="(flower, index) in flowers" :key="index" >
            <el-col>
              <div v-for="(item, index) in items" :key="index">
                <img class="plus" src="../assets/plus.png" :style="{ 'display': index > 0 ? 'block' : 'none' }">
                <el-card class="flower_card" :body-style="{ padding: '6px 0', display: 'flex' }">
                  <img :src="`../assets/flower_${item.message}_red.png`">
                  <div><div>RRyyWWSs</div><div>种子</div></div>
                </el-card>
              </div>
            </el-col>
            <img class="arrow" src="../assets/arrow.png">
            <el-col>
              <el-card class="flower_card" :body-style="{ padding: '6px 0', display: 'flex' }" v-for="(item, index) in items" :key="index" :style="{ 'margin-top': index > 0 ? '20px' : '0px' }">
                <img src="../assets/flower_rose_red.png">
                <div><div>RRyyWWSs</div><div>种子</div></div>
              </el-card>
            </el-col>
          </el-row>
        </el-row>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import {flowers} from '../data/flower'
flowers.rose[0].parents[0]
export default {
  name: "flower",
  data() {
      return {
        activeName: 'Foo',
        items: [
          { message: 'rose' },
          { message: 'test' }
        ],
        flowers: flowers
      };
  },
  methods: {
      handleClick(tab, event) {
          this.flowers = flowers[tab.name]
      }
  }
}
</script>

<style scoped>
.el-row-wrap {
  flex-flow: row wrap;
}
.el-row-flower {
  padding: 8px 12px;
}
.flower_card {
    width: 132px;
    border: none;
    border-radius: 4px;
    background: #dcd1a0;
}
.flower_card img
{
  width: 52px;
  height: 52px;
}
.arrow {
    width: 24px;
    margin: 0 12px;
}
.plus {
  width: 24px;
  margin: 6px auto;
}

</style>



